<script>
import {
  mdbCard,
  mdbBreadcrumb,
  mdbBreadcrumbItem,
  mdbCardBody,
  mdbBtn,
} from "mdbvue";
import ItemCard from "../components/ItemCard";

export default {
  components: {
    mdbCard,
    mdbCardBody,
    ItemCard,
    mdbBreadcrumb,
    mdbBreadcrumbItem,
    mdbBtn,
  },
  data() {
    return {
      categoria: "",
      products: [...this.items],
    };
  },
  methods: {
    reload() {
      this.categoria = "";
      window.scrollTo(0, 375);
      this.$router.push({ path: "/productos" });
    },
    filterCategory(category) {
      this.categoria = category;
      window.scrollTo(0, 490);
    },
  },
  props: {
    items: Array,
  },
  watch: {
    categoria(nueva) {
      if (nueva != "") {
        let temp = [...this.items];
        let changes = temp.filter((item) => item.category == nueva);
        this.products = changes;
      } else {
        this.products = [...this.items];
      }
    },
  },
  mounted() {
    if (this.$route.params.category) {
      this.categoria = this.$route.params.category;
      let temp = [...this.items];
      let changes = temp.filter(
        (item) => item.category == this.$route.params.category
      );
      this.products = changes;
      document.title = "Cercas y Mallas Versatiles - Productos";
    }
  },
};
</script>

